import React from 'react'
import Helmet from 'react-helmet'
import Offerings from '../Offerings'
import Testimonials from '../Testimonials'
import PropTypes from 'prop-types'

const HomePageTemplate = ({
  title,
  heading,
  description,
  offerings,
  meta_title,
  meta_description,
  testimonials,
}) => (
    <div>
      <Helmet>
        <title>{meta_title}</title>
        <meta name='description' content={meta_description} />
      </Helmet>
      <section className='hero is-primary is-bold is-medium'>
        <div className='hero-body'>
          <div className='container'>
            <div className='columns'>
              <div className='column is-10 is-offset-1'>
                <div className='section'>
                  <h1 className='title'>
                    {title}
                  </h1>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='section section--gradient'>
        <div className='container'>

          <div className='section'>
            <div className='columns'>
              <div className='column is-10 is-offset-1'>
                <div className='content'>
                  <div>
                    <h3 className='has-text-weight-semibold is-size-2'>
                      {heading}
                    </h3>
                    <p>{description}</p>
                    assa
                  </div>
                  <Offerings gridItems={offerings.blurbs} />
                  <h2 className='has-text-weight-semibold is-size-2'>Testimonials</h2>
                  <Testimonials testimonials={testimonials} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <h1>Shri Vitthal Hindu Mandir</h1>
<p><a href="#about"> </a></p>
<h2>Welcome to Svhm</h2>
<p> </p>
<p style="text-align: left;">Shri Vitthal Hindu Mandir (SVHM) is one of Mississauga’s longest running registered non-profit devoted to bringing families together, providing a safe place of worship, assisting new comers transition into the Canadian life, and preserving old traditions while embracing new ones. </p>
<p style="text-align: left;">Established in 1996 in the beautiful city of Mississauga, Ontario, Canada — one of Canada’s fastest growing and diverse cities — we abide by the old simple code known as the “System of Vedanta” which combines religion, philosophy, and innovative nature of the Hindu's.  The Vedanta teaches us that we are all divine, regardless of creed, class, gender, and ethnicity, and that our souls divinity may be manifested through worship, contemplation, and unselfish work.  At SVHM we welcome everyone.</p>
<p style="text-align: left;">The Mandir is located close to Toronto, Brampton, Oakville, Richmond Hill, Milton, and Etobicoke. Come visit us today and join our community, all are welcome!</p>
<p>https://www.youtube.com/watch?v=8HGSDKG1Fqk</p>
<h2>Hindu temple in mississauga</h2>
<p> </p>
<p style="text-align: left;">SVHM is different from other the other temples and mandir's in and around Toronto, Richmond Hill, Brampton, and Mississauga as it is more family oriented and transparent. The temple does not fund any external political groups and adheres to CRA policies for non profit organizations. </p>
<p>"Immigrating from Northern India in 2016, I did not find a facility in Toronto that provided a place where I can pray, mediate, practice yoga, and meet other Canadian-Tibetans like myself. Then I discovered SVHM, which has been instrumental in my journey of transitioning into the Canadian lifestyle, helping me establish myself in the GTA, build a network of quality peers who share my values, and where I can peacefully pray."<br /><img src="https://shrivitthal.com/wp-content/uploads/2018/02/25497478040_140ce47f31_k.jpg" alt="25497478040_140ce47f31_k.jpg" /><br />Tsering Wangchuk<br />Tibetan-Canadian</p>
<p style="text-align: left;"> </p>
<p style="text-align: left;">SVHM also does not discriminate – you do not need to be a “Hindu” or "Indian" to attend any event. We have numerous deities from other religions such as Sikhism, Christianity, Islam, Buddhism, to name a few . Shri Vitthal truly caters to the needs of every segment in Toronto! Our extraordinary temple is a vital part of the GTA that enriches not only the attending devotees but also the community as a whole.  All donations go back to the mandir to enhance services to our community.</p>
<h3>A temple is what you want it to be. For the devotee it is the place where god lives. For the connoisseur of art, the sculpture and paintings make a temple a living museum. For a dancer the figures carved on the walls are the foundation of her art. For a musician or a poet the deity is both inspiration and the final audience. You don’t have to enter a temple to pray. You can go there to appreciate the many aspects of India’s rich and iridescent culture. ?</h3>
<h2>what we offer</h2>
<h2>Cultural Preservation</h2>
<p> </p>
<p style="text-align: left;">Through events and story telling, we keep the ancient Hindu traditions alive ensuring their adoption by the next generation of Canadians.</p>
<p>https://www.youtube.com/watch?v=mgabrMvtUrw</p>
<h2>A place of worship</h2>
<p> </p>
<p style="text-align: left;">Our primary divine figureheads are Vitthoba, Ganesh, &amp; Sai Baba however members of our community are free to worship and pray to their god/goddess of choice.  Our facility has integrated most idols and we frequently hold cross-cultural events with other non-profits and places of worship.</p>
<p>https://www.youtube.com/watch?v=Fa9dFBf5Z4A</p>
<h2>Embrace Canada</h2>
<p> </p>
<p style="text-align: left;">Throughout the year we hold numerous events that allow new immigrants and visiting dignitaries to experience Canadian culture - true north strong and free.</p>
<p style="text-align: left;">We also help newcomers transition into the Canadian life by helping them learn the language and idioms, cultural norms, and connecting them with other community events as well as government services.</p>
<p>https://www.youtube.com/watch?v=I73Hvl7nhfw</p>
    </div>
  )

HomePageTemplate.propTypes = {
  title: PropTypes.string,
  meta_title: PropTypes.string,
  meta_description: PropTypes.string,
  heading: PropTypes.string,
  description: PropTypes.string,
  offerings: PropTypes.shape({
    blurbs: PropTypes.array,
  }),
  testimonials: PropTypes.array,

}

export default HomePageTemplate
